import axios from "../util/axiosconfig";

/* Get a blog's data by its unique title*/
export const getBlog = (id: string) => {
  return new Promise(async (resolve, reject) => {
    try {
      let obj = await axios.get("/blog/" + id);
      return resolve(obj);
    } catch (e: any) {
      return reject(e);
    }
  });
};

/* Get all blogs */
export const explore = () => {
  return new Promise(async (resolve, reject) => {
    try {
      let obj = await axios.get("/blog/");
      return resolve(obj);
    } catch (e: any) {
      return reject(e);
    }
  });
};

/* Create a new Blog */
export const create = (content: string, title: string) => {
  return new Promise(async (resolve, reject) => {
    try {
      let form = new FormData();
      form.append("content", content);
      form.append("title", title);
      let obj = await axios.post("/blog/create", form);
      return resolve(obj);
    } catch (e: any) {
      return reject(e);
    }
  });
};

/* Get all of my blogs */
export const getMyBlogs = () => {
  return new Promise(async (resolve, reject) => {
    try {
      let obj = await axios.get("/blog/my-blogs/all");
      return resolve(obj);
    } catch (e: any) {
      return reject(e);
    }
  });
};
