import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
// Components
import Tile from "../components/Tile";
import { CircularProgress } from "@material-ui/core";

interface HomeProps {
  feed: Array<any>;
  user: any;
  exploreBlogs: Function;
  myBlogs: Function;
  myBlogsData: Array<any>;
}

const styles: {
  tiles: React.CSSProperties;
  container: React.CSSProperties;
  labelTag: React.CSSProperties;
  spinnerContainer: React.CSSProperties;
} = {
  tiles: {
    padding: "3rem 0",
    display: "flex",
    flexFlow: "wrap",
  },
  container: {
    display: "flex",
    flexDirection: "column",
  },
  labelTag: {
    margin: "auto",
    padding: "1rem 0 1rem 0",
    borderBottom: "solid thin #ccc",
  },
  spinnerContainer: {
    display: "flex",
    width: "100%",
    alignItems: "center",
    justifyContent: "center",
    height: "10rem",
  },
};

export default function Home(props: HomeProps) {
  const [feedLoading, setFeedLoading] = useState(true);
  const [myBlogsLoading, setMyBlogsLoading] = useState(true);

  useEffect(() => {
    if (feedLoading) {
      props.exploreBlogs();
      // Loading effect for feed
      setFeedLoading(false);
    }
    if (myBlogsLoading) {
      props.myBlogs();
      // Loading effect for my blogs
      setMyBlogsLoading(false);
    }
  }, [feedLoading, myBlogsLoading]);

  return (
    <div style={styles.container}>
      <>
        {props.user ? (
          <Link
            to="/create"
            style={{
              textDecoration: "none",
              color: "#000000",
              fontSize: "20px",
              width: "max-content",
            }}
          >
            Create a new blog
          </Link>
        ) : (
          ""
        )}
      </>
      <label style={styles.labelTag}>
        Explore new ideas, opinions and thoughts
      </label>
      {feedLoading ? (
        <div style={styles.spinnerContainer}>
          <CircularProgress />
        </div>
      ) : (
        <>
          <div style={styles.tiles}>
            {props.feed.map((blog: any, k: any) => (
              <Tile key={k} title={blog.title} id={blog._id} />
            ))}
          </div>
        </>
      )}
      <>
        {props.user && (
          <>
            <label style={styles.labelTag}>Your Blogs</label>
            {myBlogsLoading ? (
              <div style={styles.spinnerContainer}>
                <CircularProgress />
              </div>
            ) : (
              <div style={styles.tiles}>
                {props.myBlogsData.length > 0 ? (
                  props.myBlogsData.map((blog: any, k: any) => (
                    <Tile key={k} title={blog.title} id={blog._id} />
                  ))
                ) : (
                  <label>Wow such empty</label>
                )}
              </div>
            )}
          </>
        )}
      </>
    </div>
  );
}
