import React, { useState, useEffect } from "react";
// Components
import Header from "./components/Header";
// Pages
import Home from "./pages/Home";
import Create from "./pages/Create";
import Blog from "./pages/Blog";
import Signup from "./pages/Signup";
import Signin from "./pages/Signin";
/* Api */
import { getBlog, explore, getMyBlogs } from "./api/blog";
import { myProfile } from "./api/auth";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";

function App() {
  const [blog, setBlog] = useState();
  const [feed, setFeed] = useState([]);
  const [user, setUser] = useState();
  const [myBlogsData, setMyBlogs] = useState([]);

  useEffect(() => {
    if (!user) {
      getMyProfile();
    }
  }, [user]);

  /* Get a blog by its name and send it as props to Blog page */
  const getBlogByName = (id: string) => {
    return new Promise(async (resolve, reject) => {
      try {
        let obj: any = await getBlog(id);
        setBlog(obj);
        resolve(obj);
      } catch (e: any) {
        setBlog(e.response);
        reject(e.response);
      }
    });
  };

  /* Get all blogs */
  const exploreBlogs = async () => {
    try {
      let obj: any = await explore();
      setFeed(obj.data);
    } catch (e: any) {}
  };

  /* Get user profile */
  const getMyProfile = async () => {
    try {
      let obj: any = await myProfile();
      setUser(obj.data.obj);
    } catch (e) {}
  };

  /* Get my blogs */
  const myBlogs = async () => {
    try {
      let obj: any = await getMyBlogs();
      setMyBlogs(obj.data);
    } catch (e) {}
  };

  return (
    <div>
      <Router>
        <Header user={user} />
        <div className="App">
          <Switch>
            <Route path="/create">
              <Create user={user} />
            </Route>
            <Route
              path="/blog/:id"
              render={(props) => (
                <Blog {...props} getBlogByName={getBlogByName} blog={blog} />
              )}
            />
            <Route path="/auth/signup">
              <Signup />
            </Route>
            <Route path="/auth/signin">
              <Signin />
            </Route>
            <Route exact path="/">
              <Home
                feed={feed}
                user={user}
                exploreBlogs={exploreBlogs}
                myBlogs={myBlogs}
                myBlogsData={myBlogsData}
              />
            </Route>
            <Route path="*">404 error not found</Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
