import axios from "../util/axiosconfig";

/* Api to Log in to Bloggr */
export const login = (email: string, password: string) => {
  return new Promise(async (resolve, reject) => {
    try {
      let form = new FormData();
      form.append("email", email);
      form.append("password", password);
      let obj = await axios.post("/auth/login", form);
      return resolve(obj);
    } catch (e: any) {
      return reject(e);
    }
  });
};

/* Api to Sign up in to Bloggr */
export const signup = (email: string, username: string, password: string) => {
  return new Promise(async (resolve, reject) => {
    try {
      let form = new FormData();
      form.append("email", email);
      form.append("username", username);
      form.append("password", password);
      let obj = await axios.post("/auth/signup", form);
      return resolve(obj);
    } catch (e) {
      return reject(e);
    }
  });
};

/* Api to get my profile */
export const myProfile = () => {
  return new Promise(async (resolve, reject) => {
    try {
      let obj = await axios.get("/auth/my-profile");
      return resolve(obj);
    } catch (e) {
      return reject(e);
    }
  });
};
