import React from "react";
import { Link } from "react-router-dom";

const styles = {
  tile: {
    width: "24%",
    height: "15rem",
    backgroundColor: "#FFFFFF",
    borderRadius: "1px",
    boxShadow: "rgba(149, 157, 165, 0.2) 0px 8px 24px",
    marginLeft: "1%",
    marginBottom: "1rem",
    display: "flex",
    cursor: "pointer",
    textDecoration: "none",
  },
};

interface TileProps {
  title: string;
  id: string;
}

const Tile = (props: TileProps) => {
  return (
    <Link style={styles.tile} to={`/blog/${props.id}`}>
      <div
        style={{
          padding: "1rem",
          textDecoration: "none",
          color: "black",
        }}
      >
        <label
          style={{ cursor: "pointer", fontSize: "25px" }}
          className="font"
        >
          {props.title}
        </label>
      </div>
    </Link>
  );
};

export default Tile;
