import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import Displayer from "../components/Displayer";
import { CircularProgress, makeStyles } from "@material-ui/core";

interface BlogProps {
  getBlogByName: Function;
  blog: any;
  match: any;
}

const muiStyles: any = makeStyles({
  spinner: { color: "black", margin: "auto" },
});

const styles: {
  authorStyle: React.CSSProperties;
  spinnerContainer: React.CSSProperties;
} = {
  authorStyle: {
    fontSize: "12px",
  },
  spinnerContainer: {
    display: "flex",
    width: "100%",
    alignItems: "center",
    justifyContent: "center",
    height: "70vh",
  },
};

export default function Blog(props: BlogProps) {
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  /* Call the getBlog here */
  useEffect(() => {
    run();
  }, []);

  /* This part loads the blog data when this route is visited, and only then it sets loading to false */
  const run = async () => {
    await props.getBlogByName(props.match.params.id);
    setLoading(false);
  };

  /* Get date on which the blog was created */
  let created_at: any = props.blog ? new Date(props.blog.data.created_at) : "";

  return (
    <div>
      {loading ? (
        <div style={styles.spinnerContainer}>
          <CircularProgress className={muiStyles.spinner} />
        </div>
      ) : (
        <>
          {props.blog && (
            <>
              {props.blog.data.error ? (
                history.push("/404")
              ) : (
                <>
                  <h1 style={{ fontSize: "50px" }} className="headingFont">
                    {props.blog.data.title}
                  </h1>
                  <label style={styles.authorStyle}>
                    Authored by {props.blog.data.author.username}{" "}
                    {created_at.getDate() +
                      "-" +
                      parseInt(created_at.getMonth() + 1) +
                      "-" +
                      created_at.getFullYear()}
                  </label>
                  <Displayer value={JSON.parse(props.blog.data.content)} />
                </>
              )}
            </>
          )}
        </>
      )}
    </div>
  );
}
