import React, { useState } from "react";
import { Link } from "react-router-dom";
import { signup } from "../api/auth";
/* Utils */
import { saveToStore } from "../util/localstore";
import validateEmail from "../util/emailverification";

const styles: {
  container: React.CSSProperties;
  infoText: React.CSSProperties;
  labelText: React.CSSProperties;
  inputBox: React.CSSProperties;
  createButton: React.CSSProperties;
  googleButton: React.CSSProperties;
  errorCode: React.CSSProperties;
} = {
  container: {
    flexDirection: "column",
    display: "flex",
    width: "60%",
    margin: "auto",
  },
  infoText: { fontSize: "18px", color: "grey" },
  labelText: { fontSize: "18px", marginTop: "1rem", color: "#002868" },
  inputBox: {
    padding: "1rem",
    borderRadius: "10px",
    outline: "none",
    border: "solid thin #D6D6D6",
  },
  createButton: {
    marginTop: "2rem",
    padding: "1rem",
    backgroundColor: "#0364FF",
    color: "white",
    outline: "none",
    borderRadius: "10px",
    border: "none",
    cursor: "pointer",
  },
  googleButton: {
    marginTop: "0.5rem",
    padding: "1rem",
    outline: "none",
    borderRadius: "10px",
    border: "none",
    cursor: "pointer",
  },
  errorCode: {
    height: "1rem",
    color: "#FE4747",
    userSelect: "none",
    fontSize: "13px",
    marginTop: "5px",
  },
};

function Signup() {
  /* Error code hooks */
  const [usernameError, setUsernameError] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const [responseError, setResponseError] = useState("");
  /* Store variables in state */
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const signupWithEmailAndPassword = async (
    username: string,
    email: string,
    password: string,
    cpassword: string
  ) => {
    /* Clear all the errors before a request is made */
    setUsernameError("");
    setEmailError("");
    setPasswordError("");
    setConfirmPasswordError("");
    setResponseError("");

    if (!username) {
      setUsernameError("This field is required");
      return;
    }
    if (!validateEmail(email)) {
      setEmailError("Invalid Email");
      return;
    }
    if (password.length < 8) {
      setPasswordError("Password needs to be atleast 8 characters long");
      return;
    }
    if (cpassword !== password) {
      setConfirmPasswordError("Passwords do not match");
      return;
    }

    try {
      let object: any = await signup(email, username, password);
      let token = object.data.token;
      saveToStore("auth", token);
      /* Route back to main page after signup */
      window.location.href = "/";
    } catch (e: any) {
      setResponseError(e.response.data.error || "Internal server error");
    }
  };

  return (
    <div>
      <div style={styles.container}>
        <label style={styles.infoText}>Start for free</label>
        <h1 style={{ color: "#424242" }}>Sign up to Bloggr</h1>
        <label style={styles.infoText}>
          Already a member?{" "}
          <Link
            style={{ color: "#0364FF", textDecoration: "none" }}
            to="/auth/signin"
          >
            Sign in
          </Link>
        </label>
        <label style={styles.labelText}>Username</label>
        <input
          type="text"
          style={styles.inputBox}
          placeholder="Username eg Chuck Norris"
          className="font"
          onChange={(e) => setUsername(e.target.value)}
        ></input>
        <label style={styles.errorCode}>{usernameError}</label>
        <label style={styles.labelText}>Email</label>
        <input
          type="email"
          style={styles.inputBox}
          placeholder="name@email.com"
          className="font"
          onChange={(e) => setEmail(e.target.value)}
        ></input>
        <label style={styles.errorCode}>{emailError}</label>
        <label style={styles.labelText}>Password</label>
        <input
          type="password"
          style={styles.inputBox}
          className="font"
          placeholder="8+ Characters, 1 Capital"
          onChange={(e) => setPassword(e.target.value)}
        ></input>
        <label style={styles.errorCode}>{passwordError}</label>
        <label style={styles.labelText}>Confirm Password</label>
        <input
          type="password"
          style={styles.inputBox}
          className="font"
          placeholder="Confirm your password"
          onChange={(e) => setConfirmPassword(e.target.value)}
        ></input>
        <label style={styles.errorCode}>{confirmPasswordError}</label>
        <button
          style={styles.createButton}
          className="font"
          onClick={() =>
            signupWithEmailAndPassword(
              username,
              email,
              password,
              confirmPassword
            )
          }
        >
          Create an account
        </button>
        <button style={styles.googleButton} className="font">
          Sign up with Google
        </button>
        <div style={{ margin: "auto" }}>
          <label style={styles.errorCode}>{responseError}</label>
        </div>
      </div>
    </div>
  );
}

export default Signup;
