import React, { useState } from "react";
import Editor from "../components/Editor";
import { create } from "../api/blog";

const styles: {
  row: React.CSSProperties;
  blogName: React.CSSProperties;
  createButton: React.CSSProperties;
  container: React.CSSProperties;
  errorCode: React.CSSProperties;
  titleError: React.CSSProperties;
} = {
  container: { display: "flex", flexDirection: "column" },
  row: {
    display: "flex",
    alignItems: "center",
    marginBottom: "1rem",
  },
  blogName: {
    width: "20em",
    marginLeft: "1rem",
    outline: "none",
    border: "solid thin #acacac",
    padding: "0.4rem",
    borderRadius: "2px",
  },
  createButton: {
    padding: "1rem 2rem",
    cursor: "pointer",
    border: "none",
    marginLeft: "auto",
  },
  errorCode: {
    padding: "0.7rem 0",
    color: "#FE4747",
    marginLeft: "auto",
  },
  titleError: {
    padding: "0 1rem",
    color: "#FE4747",
  },
};

interface CreateProps {
  foo?: any;
  user: any;
}

export default function Create(props: CreateProps) {
  const [value, setValue] = useState(initialValue);
  const [title, setTitle] = useState("");
  const [error, setError] = useState("");

  const createBlog = async (content: any, title: string) => {
    try {
      let c = JSON.stringify(content);
      await create(c, title);
      /* Route to homepage */
      window.location.href = "/";
    } catch (e: any) {
      setError(e.response.data.error);
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.row}>
        <label>Title</label>
        <input
          type="text"
          style={styles.blogName}
          onChange={(e) => setTitle(e.target.value)}
        ></input>
      </div>
      <div style={{ backgroundColor: "#FAFAFA", padding: "1rem 3rem" }}>
        <Editor value={value} setValue={setValue} />
      </div>
      <label style={styles.errorCode}>{error}</label>
      <button
        className="font"
        style={styles.createButton}
        onClick={() => {
          if (value[0].children[0].text.length > 0) {
            createBlog(value, title);
          }
        }}
      >
        Create
      </button>
    </div>
  );
}

const initialValue = [
  {
    type: "paragraph",
    children: [
      { text: "This is editable " },
      { text: "rich", bold: true },
      { text: " text, " },
      { text: "much", italic: true },
      { text: " better than a " },
      { text: "<textarea>", code: true },
      { text: "!" },
    ],
  },
  {
    type: "paragraph",
    children: [
      {
        text: "Since it's rich text, you can do things like turn a selection of text ",
      },
      { text: "bold", bold: true },
      {
        text: ", or add a semantically rendered block quote in the middle of the page, like this:",
      },
    ],
  },
  {
    type: "block-quote",
    children: [{ text: "A wise quote." }],
  },
  {
    type: "paragraph",
    children: [{ text: "Try it out for yourself!" }],
  },
];
