import React, { useState } from "react";
/* APIs */
import { login } from "../api/auth";
/* Utils */
import { saveToStore } from "../util/localstore";

/* Custom styles object */
const styles: {
  container: React.CSSProperties;
  infoText: React.CSSProperties;
  labelText: React.CSSProperties;
  inputBox: React.CSSProperties;
  createButton: React.CSSProperties;
  googleButton: React.CSSProperties;
  errorCode: React.CSSProperties;
} = {
  container: {
    flexDirection: "column",
    display: "flex",
    width: "60%",
    margin: "auto",
  },
  infoText: { fontSize: "18px", color: "grey" },
  labelText: { fontSize: "18px", marginTop: "2rem", color: "#002868" },
  inputBox: {
    padding: "1rem",
    borderRadius: "10px",
    outline: "none",
    border: "solid thin #D6D6D6",
  },
  createButton: {
    marginTop: "1rem",
    padding: "1rem",
    backgroundColor: "#0364FF",
    color: "white",
    outline: "none",
    borderRadius: "10px",
    border: "none",
    cursor: "pointer",
  },
  googleButton: {
    marginTop: "0.5rem",
    padding: "1rem",
    outline: "none",
    borderRadius: "10px",
    border: "none",
    cursor: "pointer",
  },
  errorCode: {
    margin: "1rem auto 0 auto",
    color: "#FE4747",
    userSelect: "none",
    height: "1.5rem",
    fontSize: "13px"
  },
};

function Signin() {
  /* Email and password hooks */
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  /* ErrorCode hook */
  const [errorCode, setErrorCode] = useState("");

  /* Signin method with just email and password */
  const loginWithEmailAndPassword = async (email: string, password: string) => {
    try {
      setErrorCode("");
      let object: any = await login(email, password);
      let token = object.data.token;
      saveToStore("auth", token);
      /* Route back to main page after signin */
      window.location.href = "/";
    } catch (e: any) {
      if (e.response.status === 401) {
        setErrorCode("Invalid email or password");
      }
    }
  };

  return (
    <div>
      <div style={styles.container}>
        <label style={styles.infoText}>Hello User !</label>
        <h1 style={{ color: "#424242" }}>Sign in to Bloggr</h1>
        <label style={styles.infoText}>Sign in to create your blog</label>
        <label style={styles.labelText}>Email</label>
        <input
          type="email"
          style={styles.inputBox}
          placeholder="name@email.com"
          className="font"
          onChange={(e) => setEmail(e.target.value)}
        ></input>
        <label style={styles.labelText}>Password</label>
        <input
          type="password"
          style={styles.inputBox}
          className="font"
          placeholder="Your password"
          onChange={(e) => setPassword(e.target.value)}
        ></input>
        <label style={styles.errorCode}>{errorCode}</label>
        <button
          style={styles.createButton}
          className="font"
          onClick={() => loginWithEmailAndPassword(email, password)}
        >
          Sign in
        </button>
        <button style={styles.googleButton} className="font">
          Sign in with Google
        </button>
      </div>
    </div>
  );
}

export default Signin;
